html,
body{
  width: 100%;
  margin:0;
}

/****APP***/
#App{
  position: relative;
}

/*NAVBAR*************************/

.navbar-brand img{
  height: 5vw;
}

.nav-item{
  text-align: center;
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus{
  border-top: 0.1875rem solid orange
}

@media (min-width: 992px){
  .navbar-expand-lg .navbar-collapse{
    max-width: 60vw;
  }

.navbar-expand-lg .navbar-collapse > ul {
    justify-self: flex-end;
    flex-grow: 1;
    justify-content: space-evenly;
}
}

/*HEADER*************************/


#header-wrapper{
  position: relative;
  background: url(blake-connally-B3l0g6HLxr8-unsplash.jpg) center no-repeat;
  background-size: cover;
  background-blend-mode: darken;
  height: 90vh;
}

#info{
  position: relative;
  top: 30%;
  color: orange;
  align-items: center;
}

.typed-text-container{
  text-align: center;
  color:white;
}

/****ABOUT ME******/
#about-me{
  height: 60vh;
  background-color: white;
}

#my-img{
  display: flex;
  justify-content: center;
  align-items: center;
}

#my-img > img{
  width: 20vw;
  border-radius: 60%;
  border: 1px solid black;
  background-color: burlywood;
}

#about-content{
  position: relative;
  top: 20%;
  text-align: justify;
  width: 30%;
}

#about-content>h1{
  color: rgb(250, 171, 24)
}


@media (max-width: 540px) {
  #about-me{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  #about-content{
    width: 70%  
  }

  #my-img{
    position: relative;
    top: 10%;
  }
  #about-content h1{
    text-align: center;  
  }
}

/*****Certificate and Experience******/
#certifications{
  background-image: linear-gradient(to bottom, black,rgb(194, 190, 190));
  height: auto;
  padding-bottom: 5%;
}

h1{
  text-align: center;
  color: orange;
}

.items-container{
  position: relative;
  top: 10%;
  width: 90%;
  margin: auto;
  border-left: 3px solid orange;
  padding-left: 10px;
}

.item{
  display: flex;
  align-items: center;
  margin:5% 0% ;
  border: 1px solid black;
  width: fit-content;
  background-color: white;
  border-radius: 10px;
  padding-right: 10px;
}

.item>p{
  margin: 0;
  font-size: 20px;
}

.circle{
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: white;
  border: 1px solid orange;
  position: relative;
  left: -17px;
}

/*****MOBILE FIX DRAG******/
@media (max-width: 540px) {
}
/********Portfolio*********/
#projects-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding-top: 5%;
  margin-bottom: 5%;
}

@media (min-width: 540px) {
  #projects-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5%;
    padding-top: 5%;
    justify-content: center;
  }
}

/**********Footer************/
#footer{
  background-color: #242424;
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: fit-content;
}
#footer p{
  color:white;
}

.input-area{
  display: flex;
  flex-direction: column;
}

.input-area button{
  width: 100%;
}


@media (min-width: 772px) {
  
}

/*******Button*********/
#button{
  background-color: white;
  align-self: center;
}

#button:link,
#button:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 10px 20px;
    display: inline-block;
    border-radius: 100px;
    transition: all .2s;
    position: absolute;
}

#button:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

#button:active {
    transform: translateY(-1px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

#button-white {
    background-color: #fff;
    color: #777;
}

#button::after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all .4s;
}

.btn-white::after {
    background-color: #fff;
}

.btn:hover::after {
    transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
}

.btn-animated {
    animation: moveInBottom 5s ease-out;
    animation-fill-mode: backwards;
}

@keyframes moveInBottom {
    0% {
        opacity: 0;
        transform: translateY(30px);
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}
